import { i18n } from '@lingui/core'
import { DatePickerInlineProps, InlineDatePicker } from 'material-ui-pickers'
import React from 'react'

import { DateTimeFormatProvider } from './DateTimeFormatProvider'

interface IProps extends DatePickerInlineProps {}

export const InputDate: React.FC<IProps> = props => {
  return (
    <DateTimeFormatProvider>
      <InlineDatePicker
        format="ll"
        maxDateMessage={i18n.t`Date is too much in the future`}
        minDateMessage={i18n.t`Date is too much in the past`}
        {...props}
      />
    </DateTimeFormatProvider>
  )
}
