import { InlineTimePicker, TimePickerInlineProps } from 'material-ui-pickers'
import React from 'react'

import { DateTimeFormatProvider } from './DateTimeFormatProvider'

interface IProps extends TimePickerInlineProps {}

export const InputTime: React.FC<IProps> = props => {
  return (
    <DateTimeFormatProvider>
      <InlineTimePicker format="LT" {...props} />
    </DateTimeFormatProvider>
  )
}
